html {
  font-size: 100%;
  scroll-behavior: smooth;
}

*, *::before, *::after {
  box-sizing: border-box;
}

* {
  padding: 0;
  margin: 0;
  font: inherit;
}

body {
  min-height: 100vh;
  font-family: var(--font-inter);
  background-color: var(--main-bg);
  color: var(--main-fc);
}

img, picture, svg, video {
  display: block;
  max-width: 100%;
}

h1, h2 {
  font-weight: 700;
}

h1 {
  font-size: clamp(1.5rem, 4vw, 1.75rem);
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5625rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-bg-start: hsl(37, 97%, 70%);
  --instagram-bg-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(146, 68%, 55%);
  --toggle-bg-end: hsl(210, 78%, 56%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --dark-main-bg: hsl(230, 17%, 14%);
  --dark-header-bg: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-main-fc: hsl(0, 0%, 100%);
  --dark-secondary-fc: hsl(228, 34%, 66%);
  --light-main-bg: hsl(0, 0%, 100%);
  --light-header-bg: hsl(225, 100%, 98%);
  --light-card-bg: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-main-fc: hsl(230, 17%, 14%);
  --light-secondary-fc: hsl(228, 12%, 44%);
}

.light {
  --main-bg: var(--light-main-bg);
  --header-bg: var(--light-header-bg);
  --card-bg: var(--light-card-bg);
  --card-hover: var(--light-card-hover);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-bg-hover: linear-gradient(to right, var(--toggle-bg-end), var(--toggle-bg-start));
  --main-fc: var(--light-main-fc);
  --secondary-fc: var(--light-secondary-fc);
}

.dark {
  --main-bg: var(--dark-main-bg);
  --header-bg: var(--dark-header-bg);
  --card-bg: var(--dark-card-bg);
  --card-hover: var(--dark-card-hover);
  --toggle-bg: linear-gradient(to right, var(--toggle-bg-end), var(--toggle-bg-start));
  --toggle-bg-hover: var(--toggle-bg-light);
  --main-fc: var(--dark-main-fc);
  --secondary-fc: var(--dark-secondary-fc);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  max-width: 70.9375rem;
  margin: 0 auto 2.875rem;
  padding: 0 1.5625rem;
}

.header {
  padding-top: 2.25rem;
  padding-bottom: 6.25rem;
  margin-bottom: -6.25rem;
  background-color: var(--header-bg);
}
.header__heading {
  padding-bottom: 1.5625rem;
  margin-bottom: 0.9375rem;
  border-bottom: 0.0625rem solid var(--secondary-fc);
}
.header__title {
  margin-bottom: 0.3125rem;
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--secondary-fc);
}
@media (min-width: 40rem) {
  .header__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header__heading {
    border: none;
    padding-bottom: 0;
    margin-bottom: 0;
  }
}

.toggle {
  border: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.875rem;
  color: var(--secondary-fc);
}
@media (min-width: 40rem) {
  .toggle {
    display: flex;
    align-items: center;
    gap: 0.875rem;
  }
}
.toggle:hover {
  color: var(--main-fc);
}
.toggle label {
  font-weight: 700;
  font-size: 0.875rem;
  cursor: pointer;
}
.toggle__wrapper {
  position: relative;
  border-radius: 0.75rem;
  overflow: hidden;
  height: 1.5rem;
  width: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.toggle__wrapper:hover .toggle__background {
  background: var(--toggle-bg-hover);
}
.toggle input[type=radio] {
  position: relative;
  z-index: 1;
  opacity: 0;
  width: 50%;
  height: 100%;
  cursor: pointer;
}
.toggle__background {
  position: absolute;
  inset: 0;
  background: var(--toggle-bg);
  z-index: 0;
}
.toggle__switcher {
  position: absolute;
  top: 0.2rem;
  background-color: var(--main-bg);
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  transition: all 200ms ease-in-out;
  cursor: pointer;
}
.toggle #dark:checked ~ .toggle__switcher {
  left: 0.1875rem;
  right: 100%;
}
.toggle #light:checked ~ .toggle__switcher {
  right: 0.1875rem;
  left: 1.6875rem;
}

.cards__wrapper {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40rem) {
  .cards__wrapper {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 59.375rem) {
  .cards__wrapper {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background-color: var(--card-bg);
  color: var(--secondary-fc);
  font-weight: 700;
  border-radius: 0.3125rem;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  transition: all 200ms ease-in-out;
}
.card:hover {
  background-color: var(--card-hover);
}
.card--small {
  padding: 1.875rem 1.5rem 1.5rem;
  display: grid;
  grid-template-columns: 1fr auto;
  row-gap: 1.3125rem;
}
.card--big {
  padding: 2.25rem 1.5rem 1.5rem;
  text-align: center;
}
.card--big::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.25rem;
}
.card--facebook::before {
  background-color: var(--facebook);
}
.card--twitter::before {
  background-color: var(--twitter);
}
.card--instagram::before {
  background: linear-gradient(to right, var(--instagram-bg-start), var(--instagram-bg-end));
}
.card--youtube::before {
  background-color: var(--youtube);
}
.card__platform {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.75rem;
}
.card__category {
  font-size: 0.875rem;
}
.card__icon img {
  margin-left: auto;
}
.card__username {
  font-size: 0.75rem;
  margin-left: 0.5rem;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--main-fc);
  letter-spacing: -0.125rem;
  line-height: 0.8;
}
.card__count--big {
  font-size: 3.5rem;
  margin-bottom: 0.5625rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  color: var(--secondary-fc);
  font-size: 0.75rem;
  font-weight: 400;
  letter-spacing: 0.3125rem;
  text-transform: uppercase;
}
.card__change {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  text-align: center;
  font-size: 0.75rem;
  line-height: 0.8;
}
.card__change--small {
  justify-content: end;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__number {
  margin-left: 0.25rem;
}